import * as React from 'react';
import moment from 'moment';

export default function HourMarker ({startHour, endHour, locale, zoom}) {
    const hourPixels = 60 * zoom;

    const hours = [];
    for (let hour = startHour; hour <= endHour; ++hour) {
        hours.push(hour);
    }

    return <div className="hourmarker" style={{
        position: 'relative',
        left: 0,
        top: 0,
        overflow: 'visible',
        height: (endHour - startHour) * hourPixels
    }}>
      {hours.map((hour, i) => {
        const ypos = i * hourPixels;
        return <span key={hour}><div style={{
                position: 'absolute',
                left: 0,
                top: ypos,
                marginTop: '-4px', // CSS!
                padding: 0
            }}>
                {moment().hour(hour).minute(0).toDate().toLocaleString(locale, {hour: "2-digit", minute: "2-digit"})}
            </div>
            {/*<div className="hourline"
                style={{
                    backgroundColor: '#eeeeF2', // CSS!
                    position: 'absolute',
                    left: '24px',
                    width: '6px',
                    margin: 0,
                    padding: 0,
                    height: '1px',
                    top: ypos
                }}
            />
            {hour < endHour && <div className="halvhourline"
                style={{
                    backgroundColor: '#eeeeF2', // CSS!
                    position: 'absolute',
                    left: '26px',
                    width: '4px',
                    margin: 0,
                    padding: 0,
                    height: '1px',
                    top: ypos + (hourPixels / 2) + 1
            }} /> } */}
        </span>;
    })}</div>;
}
