import * as React from 'react';
import { getLearningGoalsAndTasks, KedLearningGoalsData } from '../../apis/ked-learninggoals';
import { LearningModuleTask, TaskList } from '../course-viewer/subcomponents/task-list';
import { flatten } from '../../utils/utils';
import { LearningGoal, LearningGoalsList } from '../course-viewer/subcomponents/learning-goals-list';

interface Props {
  apiPath: string; // "/4.5667c63115ed6c03790a4c8c/12.1f95a67a15ee31ea1c4447b2.json"
  pageId: string; // sv.PageContext.pageId
}

interface State {
  data: KedLearningGoalsData | null
}

export class LearningTasks extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      data: null
    }
  }

  async componentDidMount() {
    const data = await getLearningGoalsAndTasks(
      this.props.apiPath,
      this.props.pageId);

    this.setState({ data });
  }

  render() {
    if (!this.state.data) return <div></div>;
    const { moduleName, subject, commonTasks, learningGoals, step } = this.state.data;
    //const step = undefined; // moduleName.toLowerCase().startsWith("steg ") && parseInt(moduleName.substr(5));
    const commonTasksList: LearningModuleTask[] =
      commonTasks.map(task => ({
        id: task.id,
        name: task.name,
        url: task.url,
        courseName: subject.name,
        learningGoal: step && learningGoals.length > 0 ? `${moduleName} - vergripande` : moduleName
      }));
      
    const learningGoalsList: LearningGoal[] = learningGoals.map(learningGoal => ({
      name: learningGoal.name,
      learningTasks: learningGoal.tasks.map(task => ({
        id: task.id,
        name: task.name,
        url: task.url,
        courseName: subject.name,
        learningGoal: learningGoal.name
      }))
    }));

    if (step) {
      commonTasksList.forEach(task => task.step = step);
      learningGoalsList.forEach(lg => lg.learningTasks.forEach(task => task.step = step));
    }

    return <LearningGoalsList
      commonTasks={commonTasksList}
      learningGoals={learningGoalsList} />;
  }
}
