import { KSTermPlannerWeekPlan } from 'kedbackend/models';
import { EDSCourse } from '../../contracts/eds';
import { EDSPeriod } from '../../apis/edsclient';
import { Term } from '../../contracts/ked-models';
import {
  findCourseFromColumnHeader,
  LAST_STEP_SPRING,
  LAST_STEP_AUTUMN,
  getColumnHeaderFromCourse,
  getFirstAndLastWeek
} from './termplanner-utils';
import { getTermStartAndEnd, getFirstAndLastWeekOfTerm, getSchoolMoment } from '../../utils/school-moment';
import moment from 'moment';
import { createUUID } from 'kedbackend/client';
import { termPlannerRepoKS } from '../../repos/ks-termplanner-repo';
import { KEDWeek } from '../../utils/weekutil';

export class ViewModel {
  weekPlans: KSTermPlannerWeekPlan[];
  lastSteps: KSTermPlannerWeekPlan;
  completedSteps: { [columnHeaderAndstep: string]: true };
  termGoals: { [columnHeader: string]: string };
  year: number;

  constructor(
    dbWeekPlans: KSTermPlannerWeekPlan[],
    courses: EDSCourse[]) {
    const nowMoment = moment(termPlannerRepoKS.now);
    const schoolMoment = getSchoolMoment(nowMoment);
    const [termStart, termEnd] = getTermStartAndEnd(nowMoment);
    const [firstWeek, lastWeek] = getFirstAndLastWeekOfTerm(schoolMoment.term);
    this.year = termStart.year();
    //
    // weekPlans
    //
    this.weekPlans = [];
    //let currentCompletedSteps: { [columnHeader: string]: string } = {};
    for (let weekMoment = termStart.clone().startOf('week');
      weekMoment.valueOf() < termEnd.valueOf();
      weekMoment = weekMoment.clone().add(1, 'week').startOf('week'))
    {
      const weekNumber = weekMoment.week();
      if (weekNumber >= firstWeek && weekNumber <= lastWeek) {
        const kedWeek = KEDWeek(weekMoment.year(), weekNumber);
        const weekPlan = dbWeekPlans.filter(wp =>
          wp.dateTime >= kedWeek.notBefore &&
          wp.dateTime <= kedWeek.notAfter)[0];

        this.weekPlans.push({
          ...weekPlan || {},
          id: weekPlan ? weekPlan.id : createUUID(),
          dateTime: weekPlan ? weekPlan.dateTime : weekMoment.valueOf(),
          cellValues: weekPlan ? weekPlan.cellValues : {},
          weekNumber
        });
      }
    }

    //
    // lastSteps
    //
    const lastStepWeek = firstWeek > LAST_STEP_SPRING ? LAST_STEP_AUTUMN : LAST_STEP_SPRING;
    const lastStepKedWeek = KEDWeek(termStart.year(), lastStepWeek);
    this.lastSteps = dbWeekPlans.filter(wp =>
        wp.dateTime >= lastStepKedWeek.notBefore &&
        wp.dateTime <= lastStepKedWeek.notAfter)[0] || {
      id: createUUID(),
      dateTime: moment(nowMoment).clone().week(lastStepWeek).startOf('week').valueOf(),
      cellValues: {},
      weekNumber: lastStepWeek
    };

    this.completedSteps = {};
    this.termGoals = {};
    for (const course of courses) {
      const columnHeader = getColumnHeaderFromCourse(course);
      //
      // completedSteps
      //
      for (const step of course.courseSteps) {
        if (step.isCompleted) {
          this.completedSteps[columnHeader + step.shortName] = true;
          /*const weekPlans = findWeekPlansByStep(this.weekPlans, step.ShortName);
          weekPlans.forEach(weekPlan => {
              this.completedSteps[weekPlan.week + columnHeader] = true;
          })*/
        }
      }
      //
      // termGoals
      //
      this.termGoals[columnHeader] = course.periodGoalGrade;
    }
  }
}

function findWeekPlansByStep(weekPlans: KSTermPlannerWeekPlan[], step: string): KSTermPlannerWeekPlan[] {
  return weekPlans.filter(wp => Object.keys(wp.cellValues).map(column => wp.cellValues[column] === step));
}
