import * as React from 'react';
import { L } from '../../utils/utils';
import { ViewModel } from './goals-viewmodel';
import env from '../../globals/KED.env';
import { EDSStudyPlan } from '../../contracts/eds';
import { FutureAbilitiesTable } from './future-abilities-table';

interface State {
  loadingStudyPlans: boolean,
  studyPlans: EDSStudyPlan[],
  error: string | null
}

export class KSGoals extends React.Component<{}, State> {
  constructor(props) {
    super(props);
    this.state = {
      loadingStudyPlans: false,
      studyPlans: [],
      error: null
    }
  }

  componentWillMount() {
    this.load();
  }

  async load() {
    this.setState({loadingStudyPlans: true});
    try {
      const studyPlans = await env.edsClient.getStudentGoals();
      this.setState({studyPlans});
    } catch (error) {
      this.setState({error});
    }
    this.setState({loadingStudyPlans: false});
  }

  render() {
    const viewModel = new ViewModel(this.state.studyPlans);
    const showTermGoals =
       (''+env.currentUser.schoolType).toLowerCase() !== "gymnasium";

    return <div className="ked_boxed">
      <h4>
        {L`Ml`}
      </h4>
      <hr />
      <table className="smallFont">
        <thead>
          <tr>
            <th>{L`mne`}</th>
            {showTermGoals && <th>{L`HT`}</th>}
            {showTermGoals && <th>{L`VT`}</th>}
            <th>{L`Slutml`}</th>
            <th>{L`Strategier`}</th>
          </tr>
        </thead>
        <tbody>
          {viewModel.subjects.map((s, i) =>
          <tr key={s.name} className={i % 2 && "tableOdd"}>
            <td className="goalsSubject">{s.name}</td>
            {showTermGoals && <td className="goalsTerm">{s.atGoal}</td>}
            {showTermGoals && <td className="goalsTerm">{s.stGoal}</td>}
            <td className="goalsEnd">{s.finalGoal}</td>
            <td>{s.strategies}</td>
          </tr>
          )}
        </tbody></table>
        <div className="divider" />
        <FutureAbilitiesTable />
    </div>
  }
}
