
export interface KedLearningGoalsData {
  moduleName: string
  subject: {
    name: string
    course: {
      name: string
      url: string
    }
  }
  step?: number
  commonTasks: KedLearningTask[]
  learningGoals: KedLearningGoal[]
}

export interface KedLearningTask {
  id: string;
  name: string;
  url: string;
}

export interface KedLearningGoal {
  name: string;
  tasks: KedLearningTask[];
}

export async function getLearningGoalsAndTasks(apiPath: string, pageId: string) {
  const res = await fetch (`${apiPath}?nodeID=${pageId}`, {credentials: "same-origin"});
  const data = await res.json();
  return data as KedLearningGoalsData;
}
