import {Term} from '../../contracts/ked-models';
import {EDSCourse} from '../../contracts/eds';

export function getFirstAndLastWeek(term: Term) {
  return term === 'AT' ?
    [33, 50] :  // This is a guess! Need data! Should be possible to get from skola24?
    [2, 23];    // This is a guess! Need data!
}

export const LAST_STEP_SPRING = 28; // Week 28 is in summer. Use it as placeholder for "last step"
export const LAST_STEP_AUTUMN = 52; // Week must be valid. Use it as placeholder for "last step"

/**
 * Note that this function is tightly coupled to the swedish school.
 */
export function findCourseFromColumnHeader(
  courses: EDSCourse[],
  columnHeader: string) : EDSCourse | null
{
  switch (columnHeader) {
    case 'M.spr': return courses
      .filter(course => course.shortName !== 'MA' && course.shortName.startsWith('M'))[0] || null;
    case 'Ma': return courses.filter(c => c.shortName === "MA")[0] || null;
    case 'Sv/SvA': return courses.filter(c => c.shortName.startsWith("SV"))[0] || null;
    case 'Eng': return courses.filter(c => c.shortName === "EN")[0] || null;
  }
  return null;
}

/**
 * Note that this function is tightly coupled to the swedish school.
 */
export function getColumnHeaderFromCourse(course: EDSCourse) : string
{
  const shortName = course.shortName;
  if (shortName === "MA") return 'Ma';
  if (shortName === "EN") return 'Eng';
  if (shortName.startsWith("SV")) return 'Sv/SvA';
  if (shortName.startsWith("M")) return 'M.spr';
  return null;
}
