// <Initialization>
import './configure'; // Emulate server-side script
import '../global-setters/set-all'; // Client-side initialization
// </Initialization>

import env from '../globals/KED.env';
import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { User } from '../contracts/ked-models';
import { learningModules } from './data/learningModules';
import { learningPageUrls } from './data/learningModules2';
import { users } from './data/users';
import { WeekPlanner } from '../components/weekplanner';
import { components } from './components';
import $ from 'jquery';
import moment from 'moment';
import {KedBackendClientWebTest} from 'kedbackend/clientwebtest';
import { ChooseUser } from './utils/choose-user';

interface State {
  selectedComponent: string,
  selectedLearningModule: number,
  selectedLearningModuleUrl: string
}

export default class TestPage extends React.Component<{},State> {
  componentArea: HTMLElement;

  constructor(props) {
    super(props);
    this.state = {
      selectedComponent: localStorage.getItem("selectedComponent") || Object.keys(components)[0],
      selectedLearningModule: parseInt(localStorage.getItem("selectedLearningModule") || "0"),
      selectedLearningModuleUrl: localStorage.getItem("selectedLearningModuleUrl") || ""
    }
  }

  componentDidUpdate() {
    console.log("component did update")
    this.renderTestComponent();
  }

  componentDidMount() {
    this.renderTestComponent();
  }

  renderTestComponent() {
    env.learningModuleTasks = learningModules[this.state.selectedLearningModule];
    $(this.componentArea).html("");

    if (env.currentUser) {
      ReactDOM.render((React as any).createElement(
        components[this.state.selectedComponent].component,
        components[this.state.selectedComponent].props(),
        null), this.componentArea);
    }
  }

  render() {
    return env.currentUser ? <div className="test-page">
      <div className="header">
        <div className="test-selects">
          <form>
            <h4>
              Testsida fr komponenter till nya startsidan
            </h4>
            <table><tbody><tr>
            <td><label htmlFor="testComponent">Komponent</label></td>
            <td><select id="testComponent" value={this.state.selectedComponent} onChange={ev => {
              this.setState({ selectedComponent: ev.target.value });
              localStorage.setItem("selectedComponent", ev.target.value);
              window.location.reload();
            }}>
              {Object.keys(components).map(c => <option key={c} value={c}>{c}</option>)}
            </select>
            </td></tr>

            {this.state.selectedComponent === 'LearningGoalsAndTasks' && <tr><td><label htmlFor="testLearningModule">Kursmodul</label></td>
            <td><select id="testLearningModule" value={this.state.selectedLearningModule} onChange={
              ev => {
                localStorage.setItem("selectedLearningModule", ev.target.value);
                window.location.reload();
              }
            }>
              {learningModules.map((lm, i) =>
                <option key={i} value={i}>{
                  lm.subject.course.name + "/" +
                  lm.subject.name + "/" +
                  lm.moduleName}</option>)
              }
            </select></td></tr>}
            {this.state.selectedComponent === 'LearningTasks' && <tr><td><label htmlFor="testLearningModuleUrl">Kursmodul</label></td>
            <td><select id="testLearningModuleUrl" value={this.state.selectedLearningModuleUrl} onChange={
              ev => {
                localStorage.setItem("selectedLearningModuleUrl", ev.target.value);
                window.location.reload();
              }
            }>
              {learningPageUrls.map((url, i) =>
                <option key={i} value={url}>{url}</option>)
              }
            </select></td></tr>}
            </tbody>
            </table>
          </form>
        </div>
      </div>
      <hr />
      <div className="componentRow">
        <div className="componentArea" ref={div => this.componentArea = div}>
        </div>
        <div className="week-planner">
          {this.state.selectedComponent === 'LearningTasks' &&
            <WeekPlanner viewCourseUrl="http://www.svt.se/nyheter" />
          }
        </div>
      </div>
    </div> : <ChooseUser />
  }
}
