import { EDSStudentFutureAbility } from '../../contracts/eds';
import { flatten } from '../../utils/utils';

type AbilityCell = { type: 'ability', name: string };
type CapabCell = { type: 'capability', name: string };
type TermCell = { type: 'term', selected: boolean };
type Cell = AbilityCell | CapabCell | TermCell;

export class FutureAbilitiesViewModel {
  columnHeaders: {name: string, type: string}[];
  vtHts: string[];
  capabRows: Cell[][];

  constructor(data: EDSStudentFutureAbility[]) {
    // Will build the total years
    const formNames: {[name: string]: true} = {};
    data.forEach(a => a.capabilities.forEach(c => formNames[c.formName] = true));
    const years = Object.keys(formNames);
    
    // columnHeaders
    this.columnHeaders = [{
      name: "Framtidsfrmga",
      type: "header"
    }, {
      name: "Omrde",
      type: "header"
    }].concat(years.map(year => ({
      name: year,
      type: 'year'
    })));

    // vtHts
    this.vtHts = flatten(years.map(year => ["HT", "VT"]));

    // cababRows
    const rows: Cell[][] = [];
    data.forEach(a => {
      let row: Cell[] = [{type: 'ability', name: a.abilityName}];
      const addedCapabs : {[name: string]: true} = {};
      a.capabilities.forEach(c => {
        const cells: TermCell[] = [
          {type: 'term', selected: c.htHasValue !== false},
          {type: 'term', selected: c.vtHasValue !== false}
        ];
        //if (c.htHasValue || c.vtHasValue) debugger;

        if (!addedCapabs[c.capabilityName]) {
          //if (!isFirstCapability) rows.push(row);
          if (row.length > 1) {
            rows.push(row);
            row = [];
          }
          addedCapabs[c.capabilityName] = true;
          row.push({type: 'capability', name: c.capabilityName});
          cells.forEach(cell => row.push(cell));
        } else if (row) {
          //addedCapabs[c.capabilityName].concat(cells);
          cells.forEach(cell => row.push(cell));
        }
      });
      if (row) rows.push(row);
    });

    this.capabRows = rows;
  }
}