import * as React from 'react';
import { SchoolTerm } from '../../utils/school-term';
import moment from 'moment';
import $ from 'jquery';
import { getSchoolMoment } from '../../utils/school-moment';
import { L } from '../../utils/utils';
import {Term} from '../../contracts/ked-models';
import {EDSPeriod} from '../../apis/edsclient';
import {EDSCourse} from '../../contracts/eds';
import {getFirstAndLastWeek, LAST_STEP_AUTUMN, LAST_STEP_SPRING} from './termplanner-utils';
import {ViewModel} from './viewmodel';
import {RenameableText} from '../utility-components/renameable-text';
import {tableWalk} from '../../utils/table-walk';
import env from '../../globals/KED.env';
import { KSTermPlannerWeekPlan } from 'kedbackend/models';
import { termPlannerRepoKS } from '../../repos/ks-termplanner-repo';

interface Props {
  initialDate: Date;
  locale: string;
}

interface State {
  schoolTerm: SchoolTerm;
  weekPlans: KSTermPlannerWeekPlan[];
  activeCourses: EDSCourse[];
  loadingWeekPlans: boolean;
  loadingActiveCourses: boolean;
  error: Error | null;
}


export class KSTermPlanner extends React.Component<Props,State> {
  table: HTMLTableElement;
  flowId: number;

  constructor(props: Props) {
    super(props);
    this.state = {
      schoolTerm: new SchoolTerm(props.initialDate),
      activeCourses: [],
      weekPlans: [],
      error: null,
      loadingActiveCourses: false,
      loadingWeekPlans: false
    }
    this.flowId = 1;
    this.updateWeekPlans = this.updateWeekPlans.bind(this);
  }

  componentWillMount() {
    this.load(this.state.schoolTerm);
  }

  componentDidMount() {
    tableWalk($(this.table));
    termPlannerRepoKS.mem.subscribe(this.updateWeekPlans);
  }

  componentWillUnmount() {
    termPlannerRepoKS.mem.unsubscribe(this.updateWeekPlans);
  }  

  updateWeekPlans(weekPlans: KSTermPlannerWeekPlan[]) {
    this.setState({weekPlans, loadingWeekPlans: false});
  }

  load(schoolTerm: SchoolTerm) {
    const flowId = ++this.flowId;
    this.setState({
      schoolTerm,
      //loadingWeekPlans: true,
      loadingActiveCourses: true
    });
    /*this.loadWeekPlans(schoolTerm).then(weekPlans => {
      if (this.flowId === flowId) {
        this.setState({weekPlans, loadingWeekPlans: false});
      }
    }).catch(error => {
      if (this.flowId === flowId) {
        this.setState({loadingWeekPlans: false, error});  
      }
    });*/
    this.loadActiveCourses(schoolTerm).then(activeCourses => {
      if (this.flowId === flowId) {
        this.setState({activeCourses, loadingActiveCourses: false});
      }
    }).catch(error => {
      if (this.flowId === flowId) {
        this.setState({loadingActiveCourses: false, error});  
      }
    });
  }

  async loadActiveCourses(schoolTerm: SchoolTerm) {
    const periodName = new EDSPeriod(schoolTerm).period;
    const activeCourses = await env.edsClient.getActiveCourses({periodName});
    return activeCourses;
  }

  async updateCell (weekPlan: KSTermPlannerWeekPlan, columnName: string, newValue: string) {
    try {
      weekPlan.cellValues[columnName] = newValue;
      return await termPlannerRepoKS.upsert (weekPlan, wp => wp.cellValues[columnName] = newValue);
    } catch (error) {
      console.error(error.stack || error);
      this.setState({error: L`Fr tillfllet problem att spara data. Dina ndringar sparades inte.`});
    }
  }

  render() {
    const model = new ViewModel(
      this.state.weekPlans,
      this.state.activeCourses);

    const {completedSteps, lastSteps, termGoals, weekPlans} = model;
    const courseColumns = ["M.spr", "Ma", "Sv/SvA", "Eng"];
    const lastTrDatas = [];

    return <div className={
        "termplanner" + 
        (this.state.loadingWeekPlans ? " loading-weekplans" : "") +
        (this.state.loadingActiveCourses ? " loading-courses" : "")
      }>
      <div className="ked_boxed">
        <div className="weekSelect">
          <div className="horizontalItem">
            <p>{this.state.schoolTerm.toLocaleString([this.props.locale], true)}</p>
          </div>
          <div className="horizontalItem">
            <div className="btn-group">
              <button className="btn" onClick={() => {
                  this.load(this.state.schoolTerm.prevTerm());
                  termPlannerRepoKS.prevTerm();
              }
              }>
                <i className="fa fa-angle-left" aria-hidden="true"></i>
              </button>
              <button className="btn" onClick={() => {
                this.load(this.state.schoolTerm.nextTerm())
                termPlannerRepoKS.nextTerm();
              }
              }>
                <i className="fa fa-angle-right" aria-hidden="true"></i>
              </button>
            </div>
          </div>
        </div>

        <h4>
          {L`Terminsplanering`}
        </h4>
        <hr />
        <table className="smallFont termplanner-table" ref={table => this.table = table} style={{
        }}>
          <thead>
            <tr>
              <th className="nowrap">{L`Vecka`}</th>
              <th className="nowrap">{L`M.spr`}</th>
              <th className="nowrap">{L`Ma`}</th>
              <th className="nowrap">{L`Sv/SvA`}</th>
              <th className="nowrap">{L`Eng`}</th>
              <th className="nowrap">{L`Kurs`}</th>
              <th>{L`Kommentar`}</th></tr>
          </thead>
          <tbody>
            {model.weekPlans.map((weekPlan, i) => {
              const {cellValues, dateTime, weekNumber} = weekPlan;
              const week = weekNumber;
              let rtxts = [];
              return <tr key={model.year + ':' + week} className={i % 2 ? "tableOdd" : ""}>
              <td>{week}</td>
              <td className={"stepCell" + (completedSteps["M.spr" + cellValues["M.spr"]] ? " completedStep" : "")}
                onClick={()=>rtxts[0].rename()}>
                <RenameableText
                  ref={rt=>rtxts.push(rt)}
                  tag="input"
                  text={cellValues["M.spr"]}
                  onChange={this.updateCell.bind(this, weekPlan, "M.spr")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </td>
              <td className={"stepCell" + (completedSteps["Ma" + cellValues["Ma"]] ? " completedStep" : "")}
                onClick={()=>rtxts[1].rename()}>
                <RenameableText
                  ref={rt=>rtxts.push(rt)}
                  text={cellValues["Ma"]}
                  onChange={this.updateCell.bind(this, weekPlan, "Ma")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </td>
              <td className={"stepCell" + (completedSteps["Sv/SvA" + cellValues["Sv/SvA"]] ? " completedStep" : "")}
                onClick={()=>rtxts[2].rename()}>
                <RenameableText
                  ref={rt=>rtxts.push(rt)}
                  text={cellValues["Sv/SvA"]}
                  onChange={this.updateCell.bind(this, weekPlan, "Sv/SvA")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </td>
              <td className={"stepCell" + (completedSteps["Eng" + cellValues["Eng"]] ? " completedStep" : "")}
                onClick={()=>rtxts[3].rename()}>
                <RenameableText
                  ref={rt=>rtxts.push(rt)}
                  text={cellValues["Eng"]}
                  onChange={this.updateCell.bind(this, weekPlan, "Eng")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </td>
              <td className="courseCell"
                onClick={()=>rtxts[4].rename()}>
                <RenameableText
                  ref={rt=>rtxts.push(rt)}
                  text={cellValues["Kurs"]}
                  onChange={this.updateCell.bind(this, weekPlan, "Kurs")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </td>
              <td onClick={()=>rtxts[5].rename()}>
                <RenameableText
                  ref={rt=>rtxts.push(rt)}
                  text={cellValues["Kommentar"]}
                  tag="textarea"
                  onChange={this.updateCell.bind(this, weekPlan, "Kommentar")} />
              </td></tr>
            }
            )}
          </tbody>
          <tfoot>
            <tr>
              <th>{L`Slutsteg`}</th>
              <th className="stepCell" onClick={()=>lastTrDatas[0].rename()}>
                <RenameableText
                  ref={rt => lastTrDatas.push(rt)}
                  text={lastSteps.cellValues["M.spr"]}
                  onChange={this.updateCell.bind(this, model.lastSteps, "M.spr")}
                  readOnly={this.state.loadingWeekPlans}
               />
              </th>
              <th className="stepCell" onClick={()=>lastTrDatas[1].rename()}>
                <RenameableText
                  ref={rt => lastTrDatas.push(rt)}
                  text={lastSteps.cellValues["Ma"]}
                  onChange={this.updateCell.bind(this, model.lastSteps, "Ma")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </th>
              <th className="stepCell" onClick={()=>lastTrDatas[2].rename()}>
                <RenameableText
                  ref={rt => lastTrDatas.push(rt)}
                  text={lastSteps.cellValues["Sv/SvA"]}
                  onChange={this.updateCell.bind(this, model.lastSteps, "Sv/SvA")}
                  readOnly={this.state.loadingWeekPlans}
                 />
              </th>
              <th className="stepCell" onClick={()=>lastTrDatas[3].rename()}>
                <RenameableText
                  ref={rt => lastTrDatas.push(rt)}
                  text={lastSteps.cellValues["Eng"]}
                  onChange={this.updateCell.bind(this, model.lastSteps, "Eng")}
                  readOnly={this.state.loadingWeekPlans}
                />
              </th>
              <th>-</th>
              <th></th>              
            </tr>
            <tr>
              <th>{L`Terminsml`}</th>
              <th className="stepCell">{termGoals["M.spr"]}</th>
              <th className="stepCell">{termGoals["Ma"]}</th>
              <th className="stepCell">{termGoals["Sv/SvA"]}</th>
              <th className="stepCell">{termGoals["Eng"]}</th>
              <th>-</th>
              <th>{this.state.error && <p className="error">{''+this.state.error}</p>}</th>
            </tr>
          </tfoot>
          </table>
      </div>
    </div>
      }
}
