import {EDSStudyPlan} from '../../contracts/eds';
import { EDSPeriod } from '../../apis/edsclient';

export class ViewModel {
    subjects: Array<{
        name: string;
        atGoal: string;
        stGoal: string;
        finalGoal: string;
        strategies: string;
    }>;

    constructor (studyPlans: EDSStudyPlan[]) {
        this.subjects = studyPlans.map(sp => {
            const periodGoals = sp.periodGoals
                .map(g => ({period: new EDSPeriod(g.periodName), goal: g.gradeName}));
            return {
                name: sp.courseUnitName,
                atGoal: periodGoals.filter (({period})=> period.term === 'AT').map(({goal}) => goal)[0],
                stGoal: periodGoals.filter (({period})=> period.term === 'ST').map(({goal}) => goal)[0],
                finalGoal: sp.finalGoalGrade,
                strategies: sp.strategyText
            };
        });
    }
}
