import * as React from 'react';
import moment from 'moment';
import DayViewEvent from './day-view-event';
import TimeLines from './time-lines';
import {MINUTE} from '../../utils/constants';
import {crunchCollidingEvents} from './crunch-colliding-events';
import { StudentCalendarEvent } from './models';

interface Props {
    dayStart: moment.Moment;   // Milliseconds at start of the day (for example 08:00 the day)
    dayEnd: moment.Moment;     // Milliseconds at end of the day (for example 17:00 the day)
    events: StudentCalendarEvent[],     // Events to show
    locale: string,
    zoom: number,
    isLastCall: boolean
}

export default function DayView ({
    dayStart,   // Milliseconds at start of the day (for example 08:00 the day)
    dayEnd,     // Milliseconds at end of the day (for example 17:00 the day)
    events,     // Events to show
    locale,
    zoom,
    isLastCall
}: Props) {
    const height = (moment(dayEnd).diff(dayStart) / MINUTE) * zoom;

    const crunchedEvents = crunchCollidingEvents(events, isLastCall);

    return (
        <div className="dayview" style={{
            top: 0,
            height,
            maxHeight: height,
            position: 'relative',
            overflow: 'hidden'
        }}>

            <TimeLines dayStart={dayStart} dayEnd={dayEnd} zoom={zoom} />

            {crunchedEvents.map (ev => <DayViewEvent
                key={ev.id}
                id={ev.id}
                className={ev.className}
                dayStart={dayStart}
                startMoment={ev.startMoment}
                endMoment={ev.endMoment}
                text={ev.text}
                location={ev.location}
                description={ev.description}
                width={ev.width}
                pos={ev.pos}
                locale={locale}
                zoom={zoom}
                htmlLink={ev.htmlLink}
            />)}
        </div>
    );
}

