import { KedRepo } from './ked-repo';
import { KSTermPlannerWeekPlan } from 'kedbackend/models';
import env from '../globals/KED.env';
import { getTermStartAndEnd } from '../utils/school-moment';
import moment from 'moment';

class KSTermPlannerRepo extends KedRepo<KSTermPlannerWeekPlan> {
  now: number;

  async nextTerm() {
    this.now = moment(this.now).add(6, 'months').valueOf();
    await this.mem.refreshFromServer();
  }

  async prevTerm() {
    this.now = moment(this.now).add(-6, 'months').valueOf();
    await this.mem.refreshFromServer();
  }

  constructor() {
    super({
      getClient() { return env.kedBackendClient; },
      optimistic: true,
      table: "weekplans-ks",
      user: env.currentUser ? env.currentUser.mail : "",
      getQueryOptions: async () => {
        const [termStart, termEnd] = getTermStartAndEnd(moment(this.now || Date.now()));
        return {
          from: termStart.valueOf(),
          to: termEnd.valueOf(),
          role: "USER"
        }
      }
    });
    this.now = Date.now();    
  }
}

export const termPlannerRepoKS = new KSTermPlannerRepo();
