import moment from 'moment';
import { flatten, distinct, compareProp } from '../../utils/utils';
import { StudentCalendarEvent } from './models';

interface EventMeta {
  index?: number,
  prevOverlaps?: StudentCalendarEvent[]  
}

export function crunchCollidingEvents (
  events: StudentCalendarEvent[],
  isLastCall?: boolean)
: StudentCalendarEvent[]
{
  const result: (StudentCalendarEvent & EventMeta)[] = [];
  events.forEach((event, eventIndex) => {
      const overlaps = result.filter(x => x.startMoment && x.endMoment &&
          (x.startMoment <= event.startMoment) &&
          (x.endMoment > event.startMoment));
      let allOverlaps = overlaps.concat(flatten (overlaps.map(o => o.prevOverlaps)));
      allOverlaps = distinct(allOverlaps, o => o.index);
      allOverlaps.sort(compareProp("startMoment"));
      let width;// = Math.round(100 / Math.max((allOverlaps.length + 1), 1));
      let pos = 0;
      allOverlaps.forEach((overlap, i) => {
        if (i % 4 === 0) {
          width = Math.max(25, Math.round(100 / Math.max(allOverlaps.length - i + 1, 1)));
          pos = 0;
        }
        overlap.width = width;
        overlap.className = (overlap.className || "").split(' ')
          .filter(cn => cn !== 'splitted')
          .concat("splitted")
          .join(' '),
        overlap.pos = pos;
        pos += width;
      });
      result.push({
        ...event,
        index: eventIndex,
        pos: pos,
        width,
        className: width < 100 ?
          (event.className ? event.className + " splitted" : "splitted") :
          event.className,
        prevOverlaps: allOverlaps
      });
      /*if (isLastCall && moment(event.start).day() === 1) {
        if (/Sve2/.test(event.text)) {
          debugger;
        }
        if (/Franska/.test(event.text)) {
          debugger;
        }
        if (/Tyska/.test(event.text)) {
          debugger;
        }
        if (/spansk/.test(event.text)) debugger;
        if (/Eng6/.test(event.text)) debugger;
      }*/
  });
  return result;
}
