import * as React from 'react';
import moment from 'moment';
import DayView from './day-view';
import HourMarker from './hour-marker';
import {capitalizeFirst, clone, L} from '../../utils/utils';
import StatusBar from './status-bar';
import { StudentCalendarEvent, DayData } from './models';

interface Props {
    firstDay: moment.Moment,  // {moment} The day to start with (normally a monday)
    startHour: number, // {number} The hour to start on
    endHour: number,   // {number} The hour to end rendering per day
    numDays: number,   // {number} Number of days, normally 5.
    events: StudentCalendarEvent[],    // Array of events to display in the week.
    locale: string,    // Locale to use for showing week days and hour marks.
    zoom: number,      // {number} Zoom where 1 means that 1 minute = 1 pixel.  
    status?: string,
    error?: Error
}

export default class WeekView extends React.Component<Props> {
    render () {
        // Props:
        const {
            firstDay,  // {moment} The day to start with (normally a monday)
            startHour, // {number} The hour to start on
            endHour,   // {number} The hour to end rendering per day
            numDays,   // {number} Number of days, normally 5.
            events,    // Array of events to display in the week.
            locale,    // Locale to use for showing week days and hour marks.
            zoom,      // {number} Zoom where 1 means that 1 minute = 1 pixel.  
            status,
            error
        } = this.props;

        // Prepare DayView datas 
        const dayDatas: DayData[] = [];
        let day = moment(firstDay);
        for (let i=0;i<numDays; ++i) {
            const dayStart = moment(day).hour(startHour);
            const dayEnd = moment(day).hour(endHour);
            const dayDate = moment(day).format('YYYY-MM-DD');
            const isToday = moment(dayStart).startOf("day").valueOf() === moment().startOf("day").valueOf();
            dayDatas.push({
                name: capitalizeFirst(dayStart.toDate().toLocaleString(locale, {
                    weekday: 'long',
                    day:'numeric',
                    month: 'short'})),
                dayStart,
                dayEnd,
                isToday,
                events: events.filter(event =>
                    event.start && event.end && event.start.dateTime && event.end.dateTime &&
                    (moment(event.start.dateTime).isBetween(dayStart, dayEnd) ||
                     moment(event.end.dateTime).isBetween(dayStart, dayEnd)))
                     // Change start / end to directly point to the dateTime
                     .map(event => clone(event, {startMoment: moment(event.start.dateTime), endMoment: moment(event.end.dateTime)}))
                     .sort((a,b) => a.startMoment.valueOf > b.startMoment.valueOf ? 1 : a.startMoment < b.startMoment ? -1 : 0),
                fullDayEvents: events.filter(event =>
                    // Verify that it is a full-day-event
                    event.start && event.end && event.start.date && event.end.date &&
                    // Verify it applies to current day
                    (event.start.date <= dayDate && event.end.date > dayDate)) 
                     // Change start / end to directly point to the date object
                    .map(event => clone(event, {
                        startDate: event.start.date,
                        endDate: event.end.date}))
            });
            day = moment(day) // clone it
                .add(1, 'days'); // Add another day
        }

        // Render
        return <div className="vemendo-weekview">
          <table><tbody>
            <tr>
                <td style={{border: 0}}></td>
                {dayDatas.map(d => <td key={d.dayStart.toISOString()} className={d.isToday ? "today" : null}>
                    <p className="dayname">{d.name}</p>
                </td>)}
            </tr><tr>
                <td style={{border: 0}}><p className="week-number">{L`V${moment(firstDay).week()}`}</p></td>
                {dayDatas.map(day =>
                    <td key={day.dayStart.toISOString()} className={day.isToday ? "today" : null}>
                        {day.fullDayEvents.map(({id, text, location, description, htmlLink, className}) =>
                            <div key={id} className={"dayviewevent full-day " + className}
                                title={`${text}` + (location ?
                                    "\n" + L`Plats: ${location}` :
                                    "") +
                                    (description ?
                                        "\n" + description :
                                        "")}
                            >
                                <a className="event-title" href={htmlLink} target="calendar-event">
                                    {text}
                                </a>
                                {location &&
                                    <p className="event-location">
                                        {location}
                                    </p>
                                }
                                {description &&
                                    <p className="event-description">
                                        {description}
                                    </p>                                
                                }
                            </div>
                        )}
                    </td>
                )}
            </tr><tr>
            <td style={{border: 0}}>
                <HourMarker
                    startHour={startHour}
                    endHour={endHour}
                    locale={locale}
                    zoom={zoom} />
            </td>
                {dayDatas.map(day => <td key={day.dayStart.toISOString()} className={day.isToday ? "today" : null}><DayView
                    key={day.dayStart.toISOString()}
                    dayStart={day.dayStart}
                    dayEnd={day.dayEnd}
                    events={day.events}
                    locale={locale}
                    zoom={zoom}
                    isLastCall={status == ''} /> </td>)}
            </tr>
            {
                (status || error) && (!error || error.name !== "UnauthorizedError") &&
                    //
                    // Show status / error
                    //
                    <tr><td style={{border: 0}}></td><td style={{border: 0}} colSpan={numDays}>
                        <StatusBar status={status} error={error} />
                    </td></tr>
            }
          </tbody></table>
        </div>
    }
}