import * as React from 'react';
import { KGTermPlannerWeekPlan, KGTermPlannerWeekPlanEntry } from 'kedbackend/models';
import {termPlannerRepo} from '../../repos/kg-termplanner-repo';
import { createUUID } from 'kedbackend/client';

interface Props {
  weekNumber: number;
  weekDate: number;
  courseName: string;
  weekPlan?: KGTermPlannerWeekPlan;
  note?: KGTermPlannerWeekPlanEntry;
  closeDialog: ()=>void;
}

interface State {
  description: string;
  color: string;
}

export class WeekNoteDialog extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      description: props.note ? props.note.description : '',
      color: props.note ? props.note.color : 'yellow'
    }
  }

  async delete() {
    const { weekNumber, weekDate, courseName, weekPlan, note, closeDialog } = this.props;
    const { description, color} = this.state;

    closeDialog();

    if (Object.keys(weekPlan.plans).length === 1) {
      await termPlannerRepo.delete([weekPlan.id]);
    } else {
      await termPlannerRepo.update([weekPlan], wp => {
        delete wp.plans[courseName]
      });
    }
  }

  async save() {
    const { weekNumber, weekDate, courseName, weekPlan, note, closeDialog } = this.props;
    const { description, color} = this.state;

    closeDialog();

    if (weekPlan) {
      await termPlannerRepo.update([weekPlan], wp => wp.plans[courseName] = {description, color});
    } else {
      const plans = {};
      plans[courseName] = {description, color};
      await termPlannerRepo.insert([{
        id: createUUID(),
        dateTime: weekDate,
        plans
      }]);
    }
  }

  render() {
    const { weekNumber, courseName, weekPlan, note } = this.props;
    const { description, color } = this.state;
    return <div>
      <div>
        <h2>Hndelse fr vecka {weekNumber} och {courseName}</h2>
        <hr />
        <div className="align-table">
          <div>Beskrivning:</div>
          <div className="fullWidth">
            <textarea autoFocus value={description} onChange={ev => this.setState({description: ev.target.value})} />
          </div>
        </div>
        <div className="align-table">
          <div>Frg:</div>
          <div>
            <select value={color} onChange={ev => this.setState({color: ev.target.value})}>
              <option value="yellow">Gul</option>
              <option value="yellowLight">Ljusgul</option>
              <option value="cyan">Grn</option>
              <option value="cyanLight">Ljusgrn</option>
              <option value="purple">Lila</option>
              <option value="purpleLight">Ljuslila</option>
              <option value="red">Rd</option>
              <option value="redLight">Rosa</option>
            </select>
          </div>
        </div>
      </div>
      <div>
        <button tabIndex={1} className="btn btn-large btn-default" onClick={()=>this.save()}>Spara</button>
        {note && <a tabIndex={1} className="btn btn-warning btn-large pull-right" onClick={()=>this.delete()}>Ta bort</a>}
      </div>
    </div>
  }
}
