import * as React from 'react';
import { L } from '../../utils/utils';
import { EDSAssessment } from '../../contracts/eds';
import env from '../../globals/KED.env';

interface Props {
  limit?: number;
  locale: string;
}

interface State {
  loading: boolean,
  assessments: EDSAssessment[],
  error: string | null
}

export class LatestAssessments extends React.Component<Props,State> {
  constructor (props: Props) {
    super(props);
    this.state = {
      assessments: [],
      error: null,
      loading: false
    }
  }

  componentWillMount() {
    this.load();
  }

  async load() {
    this.setState({loading: true});
    try {
      const assessments = await env.edsClient.getLatestAssessments(this.props.limit);
      this.setState({assessments});
    } catch (error) {
      this.setState({error});
    }
    this.setState({loading: false});    
  }

  render() {
    const hasGrades = this.state.assessments.some(a => !!a.gradeName);
    return <div className="ked_boxed">
      <div className="weekSelect">
        <div className="horizontalItem">

        </div>
        <div className="horizontalItem">

        </div>
      </div>

      <h4>{L`Senaste bedmningar`}</h4>
      <hr />
      <table className="smallFont">
        <thead>
          <tr>
            <th className="nowrap">{L`Datum`}</th>
            <th className="nowrap">{L`Kurs`}</th>
            <th className="nowrap">{L`Del`}</th>
            {hasGrades && <th className="nowrap">{L`Betyg`}</th>}
            <th className="nowrap" colSpan={2}>{L`Bedmningar`}</th>
          </tr>
        </thead>

        <tbody>
          {this.state.assessments.map((a, i) => 
            <tr key={i} className={i % 2 && "tableOdd"}>
              <td>{a.publishDateTime.substr(0, 10)}</td>
              <td className="amneCell">{a.courseName}</td>
              <td className="delCell">{a.courseUnitName}</td>
              {hasGrades && <td>{a.gradeName}</td>}
              <td><pre>{a.text}</pre></td>
            </tr>
          )}
        </tbody></table>
    </div>;
  }
}
