import * as React from 'react';
import { EDSStudentFutureAbility } from '../../contracts/eds';
import env from '../../globals/KED.env';
import { flatten } from '../../utils/utils';
import { FutureAbilitiesViewModel } from './future-abilities-viewmodel';

interface State {
  loading: boolean,
  data: EDSStudentFutureAbility[],
  error: string | null
}

export class FutureAbilitiesTable extends React.Component<{}, State> {
  constructor(props) {
    super(props);
    this.state = {
      loading: false,
      data: [],
      error: null
    };
  }

  componentWillMount() {
    this.load();
  }

  async load() {
    this.setState({ loading: true });
    try {
      const data = await env.edsClient.getStudentFutureAbilities();
      this.setState({ data });
    } catch (error) {
      this.setState({ error });
    }
    this.setState({ loading: false });
  }

  render() {
    const viewModel = new FutureAbilitiesViewModel (this.state.data);
    const {columnHeaders, vtHts, capabRows} = viewModel;

    return <div>
      <h5>Framtidsfrmgor</h5>
      <div className="partialEditStudyPlanViewMode">
        <table style={{ width: '100%' }} className="smallFont">
          <thead>
            <tr>{columnHeaders.map(({ name, type }, i) =>
              type === 'header' ?
                <th key={i} rowSpan={2}>{name}</th> :
                <th key={i} colSpan={2}>{name}</th>)}</tr>
            <tr className="header">
              {vtHts.map((vtHt, i) => <th key={i}>{vtHt}</th>)}
            </tr>
          </thead>
          <tbody>
            {capabRows.map((row, i) => <tr key={i} className={(Math.floor(i/3) % 2) && 'tableOdd'}>
              {row.map((col, i) => col.type === 'ability' ?
                <td key={i} rowSpan={3} className="goalsSubject">{col.name}</td> :
                col.type === 'capability' ?
                  <td key={i} className="goalsSubject">{col.name}</td> :
                  col.selected ?
                    <td key={i} className="edsSelected"><div className="markedItem"></div></td> :
                    <td key={i} />)}
            </tr>)}
          </tbody>
        </table>
      </div>
    </div>;
  }
}