import {KedRepo} from './ked-repo';
import { KGTermPlannerWeekPlan } from 'kedbackend/models';
import env from '../globals/KED.env';
import { getTermStartAndEnd } from '../utils/school-moment';
import moment from 'moment';

class KGTermPlannerRepo extends KedRepo<KGTermPlannerWeekPlan> {
  constructor() {
    const now = Date.now();
    const [termStart, termEnd] = getTermStartAndEnd(moment(now));
    super({
      getClient() { return env.kedBackendClient; },
      optimistic: true,
      table: "weekplans",
      user: env.currentUser ? env.currentUser.mail : "",
      getQueryOptions: async ()=>{
        return {
          from: termStart.valueOf(),
          to: termEnd.valueOf(),
          role: "USER"
        }
      }
    });
  }
}

export const termPlannerRepo = new KGTermPlannerRepo();

