import * as React from 'react';
import env from '../../globals/KED.env';
import { EDSCourse, EDSStudyPlan } from '../../contracts/eds';
import { EDSPeriod } from '../../apis/edsclient';
import { getSchoolMoment, getFirstAndLastWeekOfTerm, getTermStartAndEnd } from '../../utils/school-moment';
import moment from 'moment';
import { Spinner } from '../course-builder/sub-components/spinner';
import { KGTermPlannerWeekPlan, KGTermPlannerWeekPlanEntry } from 'kedbackend/models';
import { Dialogs } from '../utility-components/dialogs';
import { WeekNoteDialog } from './week-note-dialog';
import { termPlannerRepo } from '../../repos/kg-termplanner-repo';
import { distinct } from '../../utils/utils';
import { KEDWeek } from '../../utils/weekutil';

interface Props {
}

interface State {
  now: Date,
  courses: EDSCourse[];
  weekPlans: KGTermPlannerWeekPlan[];
  isLoadingCourses: boolean;
  isLoadingWeekPlans: boolean;
  dialogs: any[];
  //goals: EDSStudyPlan[];
}

export class KGTermPlanner extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    const now = new Date();
    this.state = {
      now,
      courses: [],
      weekPlans: [],
      isLoadingCourses: true,
      isLoadingWeekPlans: true,
      dialogs: []
      //goals: []
    }
    this.updateWeekPlans = this.updateWeekPlans.bind(this);
  }

  componentDidMount() {
    this.loadEDSCourses();
    termPlannerRepo.mem.subscribe(this.updateWeekPlans);
  }

  componentWillUnmount() {
    termPlannerRepo.mem.unsubscribe(this.updateWeekPlans);
  }

  updateWeekPlans(weekPlans: KGTermPlannerWeekPlan[]) {
    this.setState({weekPlans, isLoadingWeekPlans: false});
  }

  async loadEDSCourses() {
    try {
      const schoolMoment = getSchoolMoment(moment(this.state.now));
      const periodName = new EDSPeriod(schoolMoment).period;
      const courses = distinct(await env.edsClient.getActiveCourses({periodName}), course => course.name);
      //console.log("courses:" + JSON.stringify(courses, null, 2));
      this.setState({ courses });
    } finally {
      this.setState({ isLoadingCourses: false });
    }
  }

  onNoteClick(weekNumber: number, weekDate: number, courseName: string, weekPlan?: KGTermPlannerWeekPlan, note?: KGTermPlannerWeekPlanEntry) {
    this.openDialog(<WeekNoteDialog
      weekNumber={weekNumber}
      weekDate={weekDate}
      courseName={courseName}
      weekPlan={weekPlan}
      note={note}
      closeDialog={()=>this.closeDialog()} />);
  }

  openDialog(dialog) {
    this.setState({ dialogs: [...this.state.dialogs, dialog] });
  }

  closeDialog() {
    this.setState({ dialogs: this.state.dialogs.slice(0, this.state.dialogs.length - 1) });
  }

  render() {
    const { courses, now, isLoadingCourses, isLoadingWeekPlans, weekPlans, dialogs } = this.state;
    const isLoading = isLoadingCourses || isLoadingWeekPlans;

    const weeks: { weekNumber: number, weekDate: number, weekPlan: KGTermPlannerWeekPlan }[] = [];
    const schoolMoment = getSchoolMoment(moment(now));
    const [termStart, termEnd] = getTermStartAndEnd(moment(now));
    const [firstWeek, lastWeek] = getFirstAndLastWeekOfTerm(schoolMoment.term);

    for (let weekMoment = termStart.clone().startOf('week');
      weekMoment.valueOf() < termEnd.valueOf();
      weekMoment = weekMoment.clone().add(1, 'week').startOf('week'))
    {
      const weekNumber = weekMoment.week();
      const kedWeek = KEDWeek(weekMoment.year(), weekNumber);
      if (weekNumber >= firstWeek && weekNumber <= lastWeek) {
        const weekPlan = weekPlans.filter(wp =>
          wp.dateTime >= kedWeek.notBefore &&
          wp.dateTime <= kedWeek.notAfter)[0];

        weeks.push({
          weekNumber,
          weekDate: weekMoment.clone().startOf('week').valueOf(),
          weekPlan
        });
      }
    }

    return <div className="ked_boxed termPlanContainer">
      <h4>
        Terminsplanering
       </h4>
      <hr />
      {isLoading ?
        <Spinner /> : courses.length === 0 ? <div>
          Terminsplaneraren aktiveras nr du skrivits in p dina kurser
          </div> : <table className="smallFont">
            <thead>
              <tr>
                <th className="nowrap">Vecka</th>
                {/*goals.map(({courseUnitId, courseUnitName}) =>
                  <th key={courseUnitId} className="termCourseHeader">
                    <div className="termCourseText">{courseUnitName}</div></th>)*/}
                {
                  courses.map((c, idx) => <th key={idx} className="termCourseHeader">
                    <div className="termCourseText">{c.name}</div></th>)
                }
              </tr>
            </thead>
            <tbody>
              {weeks.map((w, idx) => 
                <tr key={idx} className={idx % 2 && 'tableOdd'}>
                  <td>{w.weekNumber}</td>
                  {courses.map((c,idx) => {
                    const note = w.weekPlan && w.weekPlan.plans[c.name];
                    return <td
                      key={idx}
                      className="courseNote"
                      title={note && note.description}
                      onClick={()=>this.onNoteClick(w.weekNumber, w.weekDate, c.name, w.weekPlan, note)}>
                      <div
                        className={`note${note ? ` noted note-color-${note.color}` : ''}`}
                      />
                    </td>}
                  )}
                </tr>
              )}
            </tbody>
            <tfoot>
              <tr>
                <th key="points">Pong</th>
                {courses.map((c, idx) => <th key={idx} className="stepCell">{c.credits}</th>)}
              </tr>
              <tr>
                <th>Betygsml</th>
                {/*goals.map(g =>
                <th className="stepCell">{g.periodGoals.filter(pg => pg.periodName ===  period.period)
                  .map(pg=>pg.gradeName)[0] || g.finalGoalGrade}</th>)*/}
                {courses.map((c, idx) =>
                  <th key={idx} className="stepCell">{c.periodGoalGrade}</th>)}
              </tr>
            </tfoot>
          </table>}
          <Dialogs
        dialogs={dialogs}
        popDialog={() => this.setState({ dialogs: dialogs.slice(0, dialogs.length - 1) })} />
          
    </div>
  }
}