import * as React from 'react';
import moment from 'moment';

export default function TimeLines ({dayStart, dayEnd, zoom}) {
    const hourPixels = 60 * zoom;
    const startHour = moment(dayStart).hour();
    const endHour = moment(dayEnd).hour();

    const hours = [];
    for (let hour = startHour; hour <= endHour; ++hour) {
        hours.push(hour);
    }

    return <div>
        {hours.map(hour => 
            <div key={hour} className="timeline" style={{
                width: '100%',
                boxSizing: 'border-box',
                margin: `${hourPixels / 2}px 0 0 0`,
                height: hourPixels / 2
        }} /> )}
    </div>;
}
