import $ from 'jquery';

export function tableWalk(tableElem: HTMLTableElement) {
    $(tableElem).keydown(ev => {
        const target = $(ev.target);

        if (ev.which === 13 && !target.is('textarea')) {
            var tabables = $(tableElem)
                .find('[tabIndex=0]:visible:not([type=checkbox])').toArray() as any[];

            var i = tabables.indexOf(ev.target);
            if (i !== -1) {
                var nextElement = tabables[(i + 1) % tabables.length];
                $(nextElement).focus();
            }
        } else if (ev.which == 38 || ev.which == 40) { // up or down
            var isUp = (ev.which == 38);
            var td = target.closest('td,th');
            var tr = td.closest('tr');
            var index = tr.children().toArray().indexOf(td[0]);
            let allTrs = $(tableElem).find('tr').toArray();
            let trIndex = allTrs.indexOf(tr[0]);
            var nextTr;
            if (isUp && trIndex > 0 && (!target.is('textarea') || target[0].selectionStart === 0)) {
                nextTr = $(allTrs[trIndex - 1]);
            } else if (!isUp && trIndex > -1 && trIndex < allTrs.length - 1 && (!target.is('textarea') || target[0].selectionEnd === target.val().length)) {
                nextTr = $(allTrs[trIndex + 1]);
            }
            if (nextTr) {
                nextTr.children().eq(index).find('[tabIndex=0]').focus();
                ev.stopPropagation();
                ev.preventDefault();
            }
        } else if (ev.which === 37 || ev.which === 39) { // left or right
            if (target.is(':input')) {
                const {selectionStart, selectionEnd, value} = target[0];
                if (selectionStart === selectionEnd) {
                    var tabables = $(tableElem)
                        .find('[tabIndex=0]:visible:not([type=checkbox])').toArray() as any[];

                    var i = tabables.indexOf(ev.target);
                    if (i !== -1 && i > 0 && ev.which === 37 && selectionStart === 0) { // left
                        let prevElement = tabables[i - 1];
                        $(prevElement).focus();
                        ev.stopPropagation();
                        ev.preventDefault();
                    } else if (i !== -1 && i < tabables.length - 1 && ev.which === 39 && selectionEnd === value.length) { // right
                        let nextElement = tabables[i + 1];
                        $(nextElement).focus();
                        ev.stopPropagation();
                        ev.preventDefault();
                    }
                }
            }
        }
    });
}
