import * as React from 'react';
import moment from 'moment';
import {MINUTE} from '../../utils/constants';
import {L} from '../../utils/utils';

interface Props {
    id: string,
    dayStart,
    startMoment: moment.Moment,
    endMoment: moment.Moment,
    text: string,
    location: string,
    description: string,
    width: number,
    pos: number,
    locale: string,
    zoom: number,
    htmlLink: string,
    className: string
}

export default function DayViewEvent ({
    id,
    dayStart,
    startMoment,
    endMoment,
    text,
    location,
    description,
    width,
    pos,
    locale,
    zoom,
    htmlLink,
    className
}: Props) {
    const top = (moment(startMoment).diff(dayStart) / MINUTE) * zoom;
    const height = (moment(endMoment).diff(startMoment) / MINUTE) * zoom;
    const localeTimeOptions = {hour: 'numeric', minute: '2-digit'};
    const localeStartTime = startMoment.toDate().toLocaleTimeString(locale, localeTimeOptions);
    const localeEndTime = endMoment.toDate().toLocaleTimeString(locale, localeTimeOptions);

    return <div className={"dayviewevent " + className} style={{
        position:'absolute',
        boxSizing: 'border-box',
        top,
        left: (pos || 0) + "%",
        width: (width || 100) + '%',
        height,
        maxHeight: height,
        overflow: 'hidden'}}

        title={`${localeStartTime} - ${localeEndTime} ${text}` +
            (location ?
                "\n" + L`Plats: ${location}` :
                "" ) +
            (description ?
                "\n" + description :
                "" )
        }
        >
        <a className="event-title" href={htmlLink} target="calendar-event">
            {text}
        </a>
        <p className="event-period">
            {localeStartTime} - {localeEndTime}
        </p>
        {location &&
            <p className="event-location">
                {location}
            </p>
        }
        {description &&
            <p className="event-description">
                {description}
            </p>
        }
    </div>;
}
