import * as React from 'react';
import { Course, UserHiddenCourse, School } from 'kedbackend/models';
import { hiddenCoursesRepo, CourseWithHiddenState } from '../../repos/hidden-courses-repo';
import env from '../../globals/KED.env';
import { flatten, compareProp, arrayToMap, arrayToLookup } from '../../utils/utils';
import { preserveImpersonationQuery } from '../../access-control';

export interface Props {
  viewCourseUrl: string;
}

export interface State {
  courses: CourseWithHiddenState[];
  isLoading: boolean;
  error?: Error;
}

export class MyCourses extends React.Component<Props,State> {

  constructor(props) {
    super(props);
    this.state = {
      courses: [],
      isLoading: true
    }
    this.updateHiddenCoursesState = this.updateHiddenCoursesState.bind(this);
  }

  componentDidMount() {
    hiddenCoursesRepo.subscribe(this.updateHiddenCoursesState, {fullCourse: false});
  }

  updateHiddenCoursesState(courses: CourseWithHiddenState[]) {
    this.setState({courses, isLoading: false});
  }


  componentWillUnmount() {
    hiddenCoursesRepo.unsubscribe(this.updateHiddenCoursesState);
  }

  getCourseUrl(courseId: string) {
    return preserveImpersonationQuery(this.props.viewCourseUrl, { courseId });
  }

  render() {
    const {viewCourseUrl} = this.props;
    const {isLoading, error, courses } = this.state;
    const visibleCourses = this.state.courses.filter(c => c.visible);

    return (
      <>
        {isLoading ? (
          <li className="lvl2 header">
            <a>Laddar...</a>
          </li>
        ) : error ? (
          <li className="lv12">
            <a>Kunde inte ladda kurser.</a>
          </li>
         ) : (
          visibleCourses.map(({name, id}) => (
            <li key={id} className="lvl2">
              <a href={this.getCourseUrl(id)}>{name}</a>
            </li>
          )
        ))
        }
			</>
    );
  }
}