import * as React from 'react';
import WeekView from './week-view';
import {GoogleCalendar} from '../../apis/google-calendar';
import moment from 'moment';
import {clone, flatten, L} from '../../utils/utils';
import StatusBar from './status-bar';
import {courseNameToCssClass} from './course-name-to-css-class';
import {shouldIncludeCalendar} from './should-include-calendar';
import { localMoment } from '../../globals/moment-sv-locale';

interface Props {
    locale: string;
    //firstDay: moment.Moment;
    numDays: number;
    initialStartHour: number;
    initialEndHour: number;
    initialZoom: number;
}

interface State {
    firstDay: moment.Moment,
    startHour: number,
    endHour: number,
    zoom: number,
    calendars: {[calendarId: string]: {
        calendar: gapi.client.calendar.CalendarListEntry,
        events: gapi.client.calendar.Event[] }},
    calendarsBeingLoaded: string[],
    status: string,
    error: Error | null 
}

export class Calendar extends React.Component<Props, State> {
    gcal: GoogleCalendar;

    constructor (props) {
        super(props);
        // Props
        const firstDay = localMoment().startOf('week').weekday(0);
        const {
            initialStartHour,
            initialEndHour,
            initialZoom
        } = this.props;
        
        // State
        this.state = {
            firstDay: firstDay,
            startHour: initialStartHour,
            endHour: initialEndHour,
            zoom: initialZoom,
            calendars: {},
            calendarsBeingLoaded: [],
            status: 'Loading calendars... (authenticating...)',
            error: null
        };

        this.gcal = new GoogleCalendar();

        // Start calling on Google API right away. Store promise in property.
        this.gcal.ensureInited().then(()=>{
            return this.loadData(firstDay);
        }).catch (error => {
            this.handleError(error);
        });
    }

    handleError(error) {
        if (!error) error = "Unknown error";
        this.setState({error});
        console.error (error.stack || error); // Keep!
    }

    loadData(firstDay) {
        // Props
        const {numDays} = this.props;
        
        this.setState({
            firstDay,
            calendars: {},
            calendarsBeingLoaded: [],
            error: null,
            status: 'Loading calendars... (listing...)'
        });

        // Important to return the Promise so that caller will catch any error.
        return this.gcal.listCalendars().then(calendars => {
            // Ignore the "week numbers" calendar as we already include week numbers in our view
            calendars = calendars.filter(cal => shouldIncludeCalendar(cal));
            //!/Week Numbers/ig.test(c.summary)

            //console.log(calendars.map(c => ({bgColor: c.backgroundColor, primary: c.primary, summary: c.summary, x: c})));
            // Calendars Successfully listed.
            this.setState({
                calendarsBeingLoaded: calendars.map(c=>c.id),
                status: `Load calendar events...`
            });

            return Promise.all(calendars.map(calendar => this.gcal.listEvents({
                calendarId: calendar.id,
                timeMin: moment(firstDay).startOf('day'),
                timeMax: moment(firstDay).add(numDays, 'days').endOf('day')
            }).then(events => {
                /*if (calendar.description === "Synchronized" || calendar.summary === 'schema') {
                    console.log(events);
                }*/
                // Successfully loaded all events from one particular calendar.
                // Now remove this calendar from 'calendarsBeingLoaded'
                const calendarsBeingLoaded =
                    this.state.calendarsBeingLoaded.filter(id => id !== calendar.id);

                // Add the loaded calendar to the state. To do this, first clone it and then set it.
                // This is needed because it is a sub property. If it was a direct property on this.state
                // react would automatically do the merge.
                const calendars = clone(this.state.calendars);
                // Append the recently loaded calendar and its events
                calendars[calendar.id] = {calendar, events};
                /*const ev = events.filter(ev => JSON.stringify(ev).indexOf("Hmta post") !== -1)
                    .map(ev => ({ev, calendar}));
                if (ev.length > 0) debugger;*/
                // Update state accordingly. This will trigger a rendering of the loaded calendar.
                // TODO: (not nescessarily) Add some sort of debounce / throttling in case multiple calendar events load fast.
                this.setState({
                    calendars,
                    calendarsBeingLoaded,
                    status: calendarsBeingLoaded.length === 0 ?
                      `` : // Empty status means everything is done!
                      `Load calendar events...`
                });
            })));
        });
    }

    navigateToPreviousWeek() {
        this.loadData(moment(this.state.firstDay).add(-1, "week")).catch(error => {
            this.handleError(error);
        });
    }

    navigateToNextWeek() {
        this.loadData(moment(this.state.firstDay).add(1, "week")).catch(error => {
            this.handleError(error);
        });
    }

    render () {
        // Props
        const { numDays, locale } = this.props;
        
        // State
        const {
            firstDay,
            startHour,
            endHour,
            zoom,
            calendars,
            status,
            error
        } = this.state;

        const eventSets = Object.keys(calendars)
            .map(calendarId => calendars[calendarId])
            .map(({calendar, events}) => {
                const isSchedule = calendar.summary === 'Skolschema' && calendar.description === "Synchronized";
                const isHoliday = /holiday/.test(calendar.id);
                const isClassRoom = /classroom/.test(calendar.id);

                const classNames = [];
                if (calendar.colorId) classNames.push("gcal-palette-" + calendar.colorId);
                if (isSchedule) classNames.push("course-event");
                if (isHoliday) classNames.push("holiday-event");
                // Map Google Calendar events to our own format:
                return events.map(event => ({
                    id: event.id,
                    start: event.start,
                    end: event.end,
                    text: event.summary,
                    location: event.location,
                    description: event.description,
                    //bgColor: calendar.backgroundColor,
                    //fgColor: calendar.foregroundColor,
                    htmlLink: event.htmlLink,
                    className: (isSchedule && event.summary ?
                        classNames.concat(courseNameToCssClass('course-event-', event.summary)) :
                        classNames).join(" ")
                }))
            });
                
        const events = flatten(eventSets);

        // Else, render the WeekView now. Any error should be displayed as red status
        return <div style={{position: 'relative', top: 0, left: 0}}>
            <div className="btn-toolbar week-navigation" >
                {/*<div className="btn-group">
                    <button className="btn">
                        {L`Idag`}
                    </button>
                </div>*/}
                <div className="btn-group">
                    <button className="btn" onClick={()=>this.navigateToPreviousWeek()}>
                        <i className="fa fa-angle-left" aria-hidden="true"></i>
                    </button>
                    <button className="btn" onClick={()=>this.navigateToNextWeek()}>
                        <i className="fa fa-angle-right" aria-hidden="true"></i>
                    </button>
                </div>
                <div className="btn-group">
                    {L`Vecka ${moment(firstDay).week()}, ${moment(firstDay).year()}`}
                </div>
            </div>
            <WeekView
                locale={locale}
                firstDay={firstDay}
                startHour={startHour}
                endHour={endHour}
                numDays={numDays}
                zoom={zoom}
                events={events}
                status={status}
                error={error}
            />
        </div>
    }
}
