/// <reference path="../../../typings-gapi/gapi.client.calendar-v3/gapi.client.calendar.d.ts" />

/* Calendars to show:

    summary: "Matsedel",
    summary: "Skolschema",
    primary: true
    id.includes('classroom')
    id.includes('holiday')
*/


export function shouldIncludeCalendar (cal: gapi.client.calendar.CalendarListEntry) {
  const summary = cal.summary || "";
  const isPrimary = !!cal.primary;
  const isClassroomCalendar = /classroom/ig.test(cal.id);
  const isHolidayCalendar = /holiday/ig.test(cal.id);

  return (
    isPrimary ||
    isClassroomCalendar ||
    isHolidayCalendar ||
    ///matsedel/ig.test(summary) ||
    /schema/ig.test(summary)
  );
}
